import React from "react";
import "./styles.css";
import productImg from "./assets/product-img.webp";
import ImageProduct from "./ImageProduct";

const ProductCard = (props) => {
  const type =
    props.type === "disable" ? (
      <div className="card">
        <img src={productImg} className="card-img-top-disable" />
        <div className="card-body">
          <p className="title-product">
            NIKE AJ1 Retro High White University Blue Jeans Sepatu Olahraga
          </p>
          <p className="price">Rp. 3.550.000</p>
          <div className="discount">
            <span className="price-discount">Rp. 1000.0000</span>
            <span className="card-discount">99%</span>
          </div>
          <button className="button-out-of-stock">Out Of stock</button>
        </div>
      </div>
    ) : (
      <div className="card">
        <img src={productImg} className="card-img-top" />
        <div className="card-body">
          <p className="title-product">
            NIKE AJ1 Retro High White University Blue Jeans Sepatu Olahraga
          </p>
          <p className="price">Rp. 3.550.000</p>
          <div className="discount">
            <span className="price-discount">Rp. 1000.0000</span>
            <span className="card-discount">99%</span>
          </div>
          <button className="button-add">Add to bag</button>
        </div>
      </div>
    );

  const size =
    props.size === "small" ? (
      <div className="card-small">
        <img src={productImg} className="card-img-top" />
        <div className="card-body">
          <p className="title-product">
            SMALL SIZE NIKE AJ1 Retro High White University Blue Jeans Sepatu
            Olahraga
          </p>
          <p className="price">Rp. 3.550.000</p>
          <div className="discount">
            <span className="price-discount">Rp. 1000.0000</span>
            <span className="card-discount">99%</span>
          </div>
          <button className="button-add-small">Add to bag</button>
        </div>
      </div>
    ) : (
      <div className="card">
        <img src={productImg} className="card-img-top" />
        <div className="card-body">
          <p className="title-product">
            NIKE AJ1 Retro High White University Blue Jeans Sepatu Olahraga
          </p>
          <p className="price">Rp. 3.550.000</p>
          <div className="discount">
            <span className="price-discount">Rp. 1000.0000</span>
            <span className="card-discount">99%</span>
          </div>
          <button className="button-add">Add to bag</button>
        </div>
      </div>
    );
    isChecked !== false && isChecked === true ? '1' : '0'
  const product = type !== size ;
  return <div>{product}</div>;
};

export default ProductCard;
